import { RendererCanvas } from '../../../lib';

import { tessellateTriangle, Vector3 } from '../../../lib/NJSCore';

export class Solution extends RendererCanvas {

    constructor(id: string) {
        super(document.getElementById(id) as HTMLDivElement);
        // TODO
        // You code goes here for one time operation

        // !! you should execute it for rendering
        this.start();
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop


        ctx.beginPath();
        ctx.arc(ctx.canvas.width * 0.5, ctx.canvas.height * 0.5, 300, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fillStyle = '#fff';
        ctx.fill()

        const cenX = ctx.canvas.width * 0.5;
        const cenY = ctx.canvas.height * 0.5;
        let myTriangle = [
            [cenX, cenY + 150, 500] as Vector3,
            [cenX - 150, cenY - 150, 0] as Vector3,
            [cenX + 150, cenY - 150, 0] as Vector3,
        ];
        
        // @ts-ignore
        let { vertices, faces } = tessellateTriangle(myTriangle, 2, 3, 4, 3);
        ctx.lineWidth = 3;
        ctx.strokeStyle = '#eee';
        for(let i = 0 ; i < faces.length; ++i) {
            ctx.beginPath();
            let v0 = vertices[faces[i][0]]
            let v1 = vertices[faces[i][1]]
            let v2 = vertices[faces[i][2]]
            ctx.moveTo(v0[0], v0[1])
            ctx.lineTo(v1[0], v1[1])
            ctx.lineTo(v2[0], v2[1])
            ctx.lineTo(v0[0], v0[1])
            ctx.closePath();
            ctx.stroke();
        }
        //   console.log(vertices);


        // !! can stop render after this frame.
        // this.isStatic = true;
    }
    public mouseDown(x: number, y: number): void {
        // TODO
    }
    public mouseUp(x: number, y: number): void {
        // TODO
    }
    public mouseDrag(x: number, y: number): void {
        // TODO
    }
    public mouseClick(x: number, y: number): void {
        // TODO
    }
    public mouseMove(x: number, y: number) {
        // TODO
    }
    public keyDown(k: KeyboardEvent): void {
        // TODO
    }
}




